//指令样式文件
import './vripple.css';
//抛出Ripple对象
export const Ripple = {
    install(Vue,option){
        Vue.directive('ripple', {
            //第一次绑定到元素时调用，进行一次性的初始化设置
            bind(el,binding) {
                //给绑定的元素添加class,创建canvas并添加到绑定元素中
                let canvas = document.createElement('canvas');
                el.classList.add('vripple-wrapper');
                canvas.classList.add('vripple-circle');
                el.appendChild(canvas);                
            },

            // 当绑定元素插入到 DOM 中。
            inserted(el, binding, vnode, oldVnode) {

                let value = binding.value||{} //取出绑定指令的值
                let color = value.color||'#fff';//取出color默认#fff              
                let height = el.offsetHeight;//绑定元素的高
                let width  = el.offsetWidth;//绑定元素的宽
                let top = el.offsetTop;//绑定元素偏移高度
                let left = el.offsetLeft;//绑定元素偏移宽度
                let canvas = el.querySelector('.vripple-circle');
                    canvas.height = height;
                    canvas.width = width;
                var ctx=canvas.getContext("2d");
                let timer;
                let startY;
                let timeout;
                el.addEventListener('touchstart',(e)=>{ 
                    let startY = e.targetTouches[0].pageY;
                    canvas.style.display = 'block';
                    clearInterval(timer);//每次点击先清除 setInterval
                    let tar = e.changedTouches[0];//touch的元素
                    let x = tar.pageX-left;//touch的点相对于元素的x
                    let y = tar.pageY-top;//touch的点相对于元素的y
                    let scale = 5;//每次放大的量
                    let radius = 2;//小圆的半径     
                    let max = height>width?height:width;//最长边的值
                    let opcity = value.opcity||0.5;//取出透明度默认0.5
                    //动画
                    timeout = setTimeout(()=>{
                        timer = setInterval(()=>{
                            opcity *= 0.96;//改变透明度
                            radius += scale;//改变半径
                            ctx.clearRect(0,0,width,height);
                            ctx.beginPath();
                            ctx.globalAlpha = opcity;
                            ctx.arc(x,y,radius,0,2*Math.PI);
                            ctx.fillStyle = color;
                            ctx.fill();
                            //当园的半径-40大于最大边长时清除动画和画布
                            if(radius-40>max){
                                clearInterval(timer);
                                ctx.clearRect(0,0,width,height);
                            }
                        },1000/60) 
                    },300)
                    

                },false);
                el.addEventListener('touchmove',e=>{
                    let moveY = e.targetTouches[0].pageY;
                    clearTimeout(timeout);
                    if(Math.abs(moveY-startY)>20){
                        canvas.style.display = 'none';
                    }
                })
                // el.addEventListener('touchend',e=>{
                //     console.log('111');
                //     ctx.clearRect(0,0,width,height);
                // })
            },
            //组件的 VNode 更新时调用
            update() {},
             //指令所在组件的 VNode 及其子VNode全部更新后调用
            componentUpdated() {},
            //只调用一次，指令与元素解绑时调用
            unbind(el) {
                let canvas = el.querySelector('.vripple-circle');
                el.removeChild(canvas);
            }
        });
    }
}
 


