
import BScroll from 'better-scroll'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components:{InlineLoading},
props: {
isloading: {	//是否出现加载图标
type: Boolean,
default: false
},
bounce: {	//当滚动超过边缘的时候会有一小段回弹动画。设置为 true 则开启动画
type: Boolean,
default: true
},

padTop: {	// 是否有上边距10px
type: Boolean,
default: true
},
scrollHeight:{	//滚动条高度100%
type:Boolean,
default:false
},
probeType: {
/**
* 有时候我们需要知道滚动的位置,默认值：0,可选值：1、2、3
* 1.会非实时屏幕滑动超过一定时间后）派发scroll 事件
* 2.会在屏幕滑动的过程中实时的派发 scroll 事件
* 3.不仅在屏幕滑动的过程中，而且在 momentum 滚动动画运行过程中实时派发 scroll 事件
*/
type: Number,
default: 1
},
click: { //点击列表是否派发click事件
type: Boolean,
default: true
},
scrollX: {  //当设置为 true 的时候，可以开启横向滚动,当设置 eventPassthrough 为 'horizontal' 的时候，该配置无效。
type: Boolean,
default: false
},
listenScroll: {	 //是否派发滚动事件
type: Boolean,
default: false
},
data: { // 类表数据
type: Array,
default: null
},
pullUpLoad: {
/**
* 这个配置用于做上拉加载功能，默认为 false
* 当设置为 true 或者是一个 Object 的时候，可以开启上拉加载
* 可以配置离（threshold）来决定开始加载的时机
* 当上拉加载数据加载完毕后，需要执行 finishPullUp 方法
*/
type: [Object,Boolean],
default: function(){
return {
threshold: -50,
stop: 50
}
}

},
pullDownRefresh: {  // 是否派发顶部下拉的事件，用于下拉刷新
/**
* 这个配置用于做下拉刷新功能，默认为 false。
* 当设置为 true 或者是一个 Object 的时候，可以开启下拉刷新
* 可以配置顶部下拉的距离（threshold） 来决定刷新时机以及回弹停留的距离（stop）。
* 当下拉刷新数据加载完毕后，需要执行 finishPullDown 方法
*/
type: [Boolean,Object],
default: false
},
refreshDelay: {
type: Number,
default: 20
},
stopPropagation: {
type: Boolean,
default: false
},
},
mounted(){


this.$nextTick(() => {
this._initScroll();
});

},
methods: {
_initScroll() {

if (!this.$refs.wrapper) {
return;
}
var u = navigator.userAgent;
var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; 
var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
let clickType;
if(isAndroid){
clickType = true;
}else{
clickType = false;
}

this.scroll = new BScroll(this.$refs.wrapper, {
probeType: this.probeType,
click: clickType,
scrollX: this.scrollX,
pullUpLoad: this.pullUpLoad,
pullDownRefresh: this.pullDownRefresh,
bounce: this.bounce,
stopPropagation: this.stopPropagation,
})


if (this.listenScroll) {
let THIS = this
this.scroll.on('scroll', (pos) => {
THIS.$emit('scroll', pos)
})
this.scroll.on('scrollEnd', (pos) => {
THIS.$emit('scrollEnd', pos,this.scroll.maxScrollY)
})
}


if (this.pullUpLoad) {
this.scroll.on('pullingUp', () => {
this.$emit('pullingUp')
})
}

if (this.pullDownRefresh) {
this.scroll.on('pullingDown', () => {

this.$emit('pullingDown')
})
}


},
finishPullUp(){
/**
* 代理better-scroll的finishPullUp方法
* 当上拉加载数据加载完毕后，需要调用此方法告诉 better-scroll 数据已加载。
*/
this.scroll && this.scroll.finishPullUp()
},
finishPullDown(){
/**
* 代理better-scroll的finishPullDown方法
* 当下拉刷新数据加载完毕后，需要调用此方法告诉 better-scroll 数据已加载。
*/
this.scroll && this.scroll.finishPullDown()
},

disable() {	//禁用 better-scroll，DOM 事件（如 touchstart、touchmove、touchend）的回调函数不再响应。

this.scroll && this.scroll.disable()
},
enable() {	//启用 better-scroll, 默认 开启。

this.scroll && this.scroll.enable()
},
refresh() {

this.scroll && this.scroll.refresh()
},
goToPage(x, y, time, easing) {

this.scroll && this.scroll.goToPage(x, y, time, easing)
},

scrollTo() {
/**
* 代理better-scroll的scrollTo方法 滚动到指定的位置
* Number} x 横轴坐标（单位 px）
* {Number} y 纵轴坐标（单位 px）
* {Number} time 滚动动画执行的时长（单位 ms）
* {Object} easing 缓动函数，一般不建议修改，如果想修改，参考源码中的 ease.js 里的写法
*/
this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
},
scrollToElement() {
/**
* 代理better-scroll的scrollToElement方法 滚动到指定的目标元素。
* scrollToElement(el, time, offsetX, offsetY, easing)
* {DOM | String} el 滚动到的目标元素, 如果是字符串，则内部会尝试调用 querySelector 转换成 DOM 对象。
* {Number} time 滚动动画执行的时长（单位 ms）
* {Number | Boolean} offsetX 相对于目标元素的横轴偏移量，如果设置为 true，则滚到目标元素的中心位置
* {Number | Boolean} offsetY 相对于目标元素的纵轴偏移量，如果设置为 true，则滚到目标元素的中心位置
* {Object} easing 缓动函数，一般不建议修改，如果想修改，参考源码中的 ease.js 里的写法
*
*/
this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
},

},
watch: {

data() {
setTimeout(() => {
this.refresh()
}, this.refreshDelay)
},
isloading() {
setTimeout(() => {
this.refresh()
}, this.refreshDelay)
}
}
}



